//import { getByTitle } from '@testing-library/dom'
//import React, { Component } from 'react'
import React from 'react'
import './NewsItem.css'
const NewsItem=(props)=>{
        let { title, description, imageUrl, newsUrl,source, author,date }=props;
        return (
            
            <div className="container my-3">
                <div className="card my-3" style={{ width: "auto" }}>
                    <div style={{display:'flex',justifyContent:'flex-end',position:'absolute',right:0}}>
                    <span className="badge rounded-pill bg-danger">{source}
                    </span>
                    </div>
                <img src={!imageUrl?"https://static.toiimg.com/thumb/msid-86286033,width-1070,height-580,imgsize-39328,resizemode-75,overlay-toi_sw,pt-32,y_pad-40/photo.jpg":imageUrl} className="card-img-top" alt="..."/>
                <div className="card-body">
                        <h6 className="card-title">{title}</h6>
                        <p className="card-text">{description}</p>
                        <p className="card-text"><small className="text-muted">By {!author?"unknown":author} on {new Date(date).toGMTString()}</small></p>
                    <a rel="noreferrer" href={newsUrl} className="btn btn-sm btn-dark">View more</a>
                </div> 
                </div>
            </div>
        )
    
}

export default NewsItem
